<template>
  <div class="home">
    {{currentBalance}}
    {{pairAB.address}}
    <div class="exchange-box">
      <div class="title flex-between">
        <span class="text">
          从
        </span>
        <span class="text">
          余额：{{tokenA.balance}}
        </span>
      </div>
      <div class="input-model flex-middle">
        <Field
          class="flex-item"
          v-model="tokenA.value"
          type="number"
          label=""
          placeholder="0.0"
        />
        <div class="coin-select flex-middle" @click="showPopup('tokenA')">
          <span class="coin-text">{{tokenA.symbol}}</span>
          <Icon name="arrow-down" size="16" />
        </div>
      </div>
      <div class="arrow-down">↓</div>
      <div class="title flex-between">
        <span class="text">
          到
        </span>
        <span class="text">
          余额：{{tokenB.balance}}
        </span>
      </div>
      <div class="input-model flex-middle">
        <Field
          class="flex-item"
          v-model="tokenB.value"
          type="number"
          label=""
          placeholder="0.0"
        />
        <div class="coin-select flex-middle">
          <span class="coin-text">{{tokenB.symbol}}</span>
          <Icon name="arrow-down" size="16" />
        </div>
      </div>
      <div class="btn-box">
        <Button type="primary" size="large" @click="approve">授权</Button>
        <Button type="primary" size="large" @click="addLiquidity">添加流动性</Button>
        <Button type="primary" size="large" @click="swap">兑换</Button>
        <Button type="primary" size="large" @click="removeLiquidity">移除流动性</Button>
      </div>
    </div>
    <Popup v-model:show="show" closeable round position="bottom">
      <div class="popup-content">
        <div class="title">选择一个代币</div>
        <Field
          class="flex-item"
          v-model="value"
          type="text"
          label=""
          placeholder="通过地址搜索"
        />
      </div>
      <div class="title">代币的名字</div>
    </Popup>
  </div>
</template>

<script>
import { reactive, toRefs, onMounted, computed, watch } from "vue";
import { Field, Icon, Button, Popup } from 'vant';
import Bignumber from "bignumber.js"
import {useStore} from 'vuex';
import erc20Abi from '@/assets/abi/erc20.json'
import routerAbi from "@/assets/abi/router.json"
import ceshiAbi from "@/assets/abi/ceshi.json"
import { pack, keccak256 } from '@ethersproject/solidity'
import { getCreate2Address } from '@ethersproject/address'

export default {
  components: {
    Field, 
    Icon, 
    Button,
    Popup
  },
  setup() {
    const store = useStore()
    const data = reactive({
      address: computed(() => store.state.accounts[0]),
      provider: computed(() => store.state.provider),
      currentBalance: computed(() => store.state.currentBalance),
      pairAB: {
        address: '',
        contract: {},
        decimals: '',
        symbol: '',
        balance: 0,
        value: ""
      },
      tokenA: {
        address: '0xc74cc783ed2dBCd06e06266E72aD9d9680Cf3CEE',
        contract: {},
        decimals: '',
        symbol: '',
        balance: 0,
        value: ""
      },
      tokenB: {
        address: '0x163f9d7a590e1921C1461bf6eD455B67E7877E95',
        contract: {},
        decimals: '',
        symbol: '',
        balance: 0,
        value: ""
      },
      approve: () => {
        Promise.all([data.tokenA.contract.methods.approve('0x07d090e7FcBC6AFaA507A3441C7c5eE507C457e6', '100000000000000000000000000').send({from: data.address}),
          data.tokenB.contract.methods.approve('0x07d090e7FcBC6AFaA507A3441C7c5eE507C457e6', '100000000000000000000000000').send({from: data.address}),
          data.pairAB.contract.methods.approve('0x07d090e7FcBC6AFaA507A3441C7c5eE507C457e6', '100000000000000000000000000').send({from: data.address})
        ]).then(()=>{
          console.log(1111)
        })
      },
      addLiquidity: async () => {
        let router = new data.provider.eth.Contract(routerAbi, "0x07d090e7FcBC6AFaA507A3441C7c5eE507C457e6")
        let tokenANumber = new Bignumber(data.tokenA.value).multipliedBy(Math.pow(10, data.tokenA.decimals)).toString()
        let tokenBNumber = new Bignumber(data.tokenB.value).multipliedBy(Math.pow(10, data.tokenB.decimals)).toString()
        let time = parseInt(new Date().getTime()/1000) + 1200 + ''
        console.log(tokenANumber, tokenBNumber)
        // let result = router.methods.addLiquidity(data.tokenA.address, data.tokenB.address,tokenANumber,tokenBNumber, tokenANumber, tokenBNumber, data.address, time).encodeABI();
        // console.log(result)
        // data.provider.eth.estimateGas({from: data.address, data: result}).then((gaslimit) => {
        //   console.log(gaslimit)
        // })
        let gas =  await router.methods.addLiquidity(data.tokenA.address, data.tokenB.address,tokenANumber,tokenBNumber, '0', '0', data.address, time).estimateGas({from: data.address})
        console.log(gas)
        router.methods.addLiquidity(data.tokenA.address, data.tokenB.address,tokenANumber,tokenBNumber, '0', '0', data.address, time).send({from: data.address, gas: 4000000}).then((res) => {
          console.log(res)
        })
        // router.methods.addLiquidityETH(data.tokenB.address,tokenBNumber, 1000, 1000, data.address, 86400000).send({from: data.address, value: tokenANumber}).then((res) => {
        //   console.log(res)
        // })
      },
      removeLiquidity: async () => {
        let router = new data.provider.eth.Contract(routerAbi, "0x07d090e7FcBC6AFaA507A3441C7c5eE507C457e6")
        let tokenANumber = new Bignumber(data.tokenA.value).multipliedBy(Math.pow(10, data.tokenA.decimals)).toString()
        let tokenBNumber = new Bignumber(data.tokenB.value).multipliedBy(Math.pow(10, data.tokenB.decimals)).toString()
        let time = parseInt(new Date().getTime()/1000) + 1200 + ''
        console.log(tokenANumber, tokenBNumber)
        // let result = router.methods.addLiquidity(data.tokenA.address, data.tokenB.address,tokenANumber,tokenBNumber, tokenANumber, tokenBNumber, data.address, time).encodeABI();
        // console.log(result)
        // data.provider.eth.estimateGas({from: data.address, data: result}).then((gaslimit) => {
        //   console.log(gaslimit)
        // })
        let gas = await router.methods.removeLiquidity(data.tokenA.address, data.tokenB.address, 1161277, '0', '0', data.address, time).estimateGas({from: data.address})
         console.log(gas)
         router.methods.removeLiquidity(data.tokenA.address, data.tokenB.address, 1161277, '0', '0', data.address, time).send({from: data.address, gas: gas}).then(() => {})
      },
      swap: async () => {
        let router = new data.provider.eth.Contract(routerAbi, "0x07d090e7FcBC6AFaA507A3441C7c5eE507C457e6")
        let tokenANumber = new Bignumber(data.tokenA.value).multipliedBy(Math.pow(10, data.tokenA.decimals)).toString()
        let time = parseInt(new Date().getTime()/1000) + 1200 + ''
        const path = [data.tokenA.address, data.tokenB.address]
        console.log(tokenANumber)
        // let result = router.methods.addLiquidity(data.tokenA.address, data.tokenB.address,tokenANumber,tokenBNumber, tokenANumber, tokenBNumber, data.address, time).encodeABI();
        // console.log(result)
        // data.provider.eth.estimateGas({from: data.address, data: result}).then((gaslimit) => {
        //   console.log(gaslimit)
        // })
        let gas = await router.methods.swapExactTokensForTokens(tokenANumber, '0',path, data.address, time).estimateGas({from: data.address})
        console.log(gas)
        router.methods.swapExactTokensForTokens(tokenANumber, '0',path, data.address, time).send({from: data.address, gas: gas}).then((res) => {

        })
      }
    });
   watch(() => data.address, () => {
     const pair = getCreate2Address(
      "0x1AedD625e70059336A1D63Bd1528DE593260f2dE",
      keccak256(['bytes'], [pack(['address', 'address'], [data.tokenA.address, data.tokenB.address])]),
      "0xda49cdf30b204ed427a1871c5e233b7e3c6613e9162489ee0e0f9b9ff2e4fd82"
     )
     data.pairAB.address = pair;
      data.tokenA.contract = new data.provider.eth.Contract(erc20Abi, data.tokenA.address)
      data.tokenB.contract = new data.provider.eth.Contract(erc20Abi, data.tokenB.address)
      data.pairAB.contract = new data.provider.eth.Contract(erc20Abi, data.pairAB.address)
      data.ceshi = new data.provider.eth.Contract(ceshiAbi, '0x6CeAEc2cF4CB667B10d35B016a4FA313ad5Dd519')
      data.tokenA.contract.methods.balanceOf(data.address).call().then(async (amount) => {
        let decimals = await data.tokenA.contract.methods.decimals().call()
        data.tokenA.decimals = decimals
        let symbol = await data.tokenA.contract.methods.symbol().call()
        data.tokenA.symbol = symbol
        data.tokenA.balance = new Bignumber(amount).div(Math.pow(10, decimals)).toNumber()
      })
      data.tokenB.contract.methods.balanceOf(data.address).call().then(async (amount) => {
        let decimals = await data.tokenB.contract.methods.decimals().call()
        let symbol = await data.tokenB.contract.methods.symbol().call()
        data.tokenB.decimals = decimals
        data.tokenB.symbol = symbol
        data.tokenB.balance = new Bignumber(amount).div(Math.pow(10, decimals)).toNumber()
      })
      data.pairAB.contract.methods.balanceOf(data.address).call().then(async (amount) => {
        let decimals = await data.pairAB.contract.methods.decimals().call()
        let symbol = await data.pairAB.contract.methods.symbol().call()
        data.pairAB.decimals = decimals
        data.pairAB.symbol = symbol
        data.pairAB.balance = new Bignumber(amount).div(Math.pow(10, decimals)).toNumber()
      })
     console.log(pair)  
     data.ceshi.methods.getEthUsd().call().then((amount) => {
       console.log('ceshi----', amount)
     })
   });
    onMounted(() => {
      // console.log(data.provider.eth)
      // data.tokenA.contract = new data.provider.eth.Contract(erc20Abi, data.tokenA.address)
      // data.tokenB.contract = new data.provider.eth.Contract(erc20Abi, data.tokenB.address)
      // data.tokenA.contract.blanceOf(data.address).then((amount) => {
      //   console.log(amount)
      // })
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>
<style lang="less" scoped>
.exchange-box {
  margin: 20px;
  background: #fff;
  border-radius: 10px;
  padding: 20px 15px;
}
.arrow-down {
  font-size: 15px;
  font-weight: bold;
  padding: 10px 0;
}
.btn-box {
  margin-top: 15px;
}
</style>
